.container{
    box-sizing: border-box;
    padding: 15px;
    margin-left: auto;
    margin-right: auto;
    max-width: 450px;
    min-width: 350px;
}
.div{  
    position: relative;
    text-align: center;

}
.backgroundAboutUs{
    width: 100%;
    height: 250px;
}
.p{
    position: absolute;
    top: 15%;
    left: 10%;
    color: white;
    margin: 0;
    width: 80%;
    font-family: 'DM Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 146.2%;
    
}
.p2{
    position: absolute;
    top: 70%;
    left: 10%;
    color: white;
    margin: 0;
    width: 80%;
    font-family: 'DM Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 146.2%;
    text-align: center;
}